@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.informacion-portafolio {
    background-color: v.$primario;
    &__panel {
        width: 90%;
        height: 100%;
        margin: 0 auto;
        padding: 1rem;
        @include m.tablet {
            padding: 4rem 2rem;
        }
    }
    &__version-h2 {
        color: v.$letrasDos;
        font-size: 2.2rem;
        list-style: none;
        text-transform: capitalize;
    }
    &__versiones-h3 {
        color: v.$letrasDos;
        font-size: 2rem;
        display: inline-block;
        margin: 1rem;
        margin-top: 0;
        padding: 0;
        text-transform: capitalize;
    }
    &__listado-versiones {
        background-color: lighten(v.$primario, 5);
        padding: 1rem;
        @include m.tablet {
            padding: 2rem 3rem;
        }
        border-radius: 8px;
    }
    &__grid {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        gap: 1rem;
        margin-left: 3rem;
    }
    &__box {
        background-color: v.$primario;
        padding: 2rem;
        border-radius: 8px;
        list-style: none;
    }
    &__hf {
        display: flex;
        flex-direction: row;
        gap: 0.4rem;
        @include m.tablet {
            gap: 2rem;
        }
    }
    &__version-parrafo,
    &__fecha {
        color: v.$letrasDos;
        font-size: 2rem;
        margin: 0.2rem 0;
        @include m.tablet {
            padding: 1rem;
        }
    }
    &__parrafo {
        margin-left: 1rem;
        color: darken(v.$secundario, 30);
    }
    &__texto {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1rem;
    }
    &__icono--error,
    &__icono--done {
        font-size: 20px;
        font-weight: 100;
    }
    &__icono--error {
        color: red;
    }
    &__icono--done {
        color: v.$verde;
    }
}