@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.destacados {
    background-color: v.$primario;
    &__panel {
        display: flex;
        flex-direction: column;
        width: 90%;
        margin: 0 auto;
        padding: 6rem 0;
    }
    &__titulo-h2 {
        margin: 0;
        padding: 0;
        padding-bottom: 2rem;
        color: v.$letrasDos;
    }
    &__resumen {
        margin: 0;
        padding: 0;
        margin-bottom: 2rem;
        color: darken(v.$secundario, 30);
        text-align: justify;
    }
    &__grid {
        margin: 0 auto;
        width: 90%;
        display: flex;
        flex-direction: column;
        gap: 6rem;
        @include m.tablet {
            flex-direction: row;
        }
    }
    &__box {
        padding: 2rem;
        transition-property: box shadow;
        transition-duration: 0.4s;
        height: fit-content;
        border: 1px solid v.$primario;
        border-radius: 8px;
        &:hover {
            border-color: v.$letrasTitulo;
            cursor: pointer;
        }
    }
    &__h3 {
        padding: 0;
        padding-bottom: 1rem;
        margin: 0;
        margin-top: 2rem;
        font-size: 2.2rem;
        text-align: start;
        color: v.$letrasTitulo;
    }
    &__texto {
        padding: 0;
        margin: 0;
        font-size: 1.6rem;
        text-align: justify;
        color: darken(v.$secundario, 30);
    }
    &__imagen {
        width: 100%;
        height: 80%;
        border-radius: 8px;
    }
}