@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.recientes-portafolio {
    background-color: v.$primario;
    // Primer bloque
    &__panelPrincipal {
        width: 90%;
        margin: 0 auto;
        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 4rem;
    }
    // Segundo bloque
    &__panelDatos {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        gap: 3rem;
    }
    &__masDatos {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 0.8rem;
    }
    // Tercero bloque
    &__panelDatosHeader {
        display: grid;
        grid-template-columns: 1fr 2fr;
        gap: 5rem;
    }
    &__informacionPrincipal {
        text-align: justify;
        font-size: 1.6rem;
        color: darken(v.$secundario, 30);
    }
    &__boxMasDatos {
        width: 100%;
        height: max-content;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;
        transition-property: border;
        transition-duration: .3s;
        border: 2px solid v.$primario;
        &:hover {
            border-color: v.$letrasTitulo;
            cursor: pointer;
        }
    }
    // Cuarto bloque
    &__contenedorImagen {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-width: 40rem;
        min-height: 30rem;
        background-image: url("../img/Proyectos/JuanBarber.avif");
        background-size: cover;
        background-position: 0 0;
        background-repeat: no-repeat;
        border-radius: 8px;
        border: 2px solid v.$primario;
    }
    &__contenedorInfPrincipal {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        gap: 2rem;
    }
    &__imagenPaginaDatos {
        display: block;
        width: 100%;
        height: 20rem;
        padding: 0;
        margin: 0;
    }
    &__h3 {
        margin: 0;
        padding: 0.2rem 1rem;
        color: v.$letrasDos;
        font-size: 2rem;
    }
    &__BreInfo {
        margin: 0;
        padding: 0.2rem 1rem 2.2rem 1rem;
        color: darken(v.$secundario, 30);
        font-size: 1.6rem;
        text-align: justify;
    }
    // Quinto bloque
    &__visitar {
        color: v.$letrasDos;
        background-color: v.$primarioOpacity;
        width: 100%;
        margin: auto;
        margin-bottom: 0;
        padding: 0.2rem 2rem;
        text-align: center;
        opacity: 0;
    }
    &__contenedorTitulo {
        display: flex;
        flex-direction: column;
        gap: .4rem;
    }
    &__contenedorPuntaje {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        gap: 10rem;
    }
    &__contenedorIntegrantes {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        gap: 1rem;
    }
    // Sexto bloque
    &__fechaActualizacion {
        font-size: 1.2rem;
        color: v.$secundario;
        margin: 0;
        padding: 0;
    }
    &__h2 {
        margin: 0;
        padding: 0;
        font-size: 2.2rem;
        color: v.$letrasDos;
        text-align: start;
    }
    &__textoVersion {
        font-size: 1.2rem;
        color: v.$secundario;
        margin: 0;
        padding: 0;
    }
    &__contenedorSEO,
    &__contenedorCal {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        color: white;
        gap: 2rem;
    }
    &__boxIntegrante {
        width: 3rem;
        height: 3rem;
        background-image: url("../img/foto-perfil.avif");
        background-size: cover;
        background-position: 0 0;
        background-repeat: no-repeat;
        filter: grayscale(100%);
    }
    // Septimo bloque
    &__boxBarraPrincipal  {
        
    }
}
