@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.habilidades {
    // background-color: v.$primario;
    @include m.bgImage( "../img/fondo-habilidades.webp", v.$primarioOpacity);
    margin-top: -2px;
    &__panel {
        display: flex;
        flex-direction: column;
        width: 90%;
        margin: 0 auto;
        padding: 6rem 0;
    }
    &__titulo-h2 {
        margin: 0;
        padding: 0;
        padding-bottom: 2rem;
        color: v.$letrasDos;
    }
    &__resumen {
        margin: 0;
        padding: 0;
        color: darken(v.$secundario, 30);
        text-align: justify;
    }
    &__grid {
        margin: 2rem 0;
        padding: 0;
        display: grid;
        grid-template-columns: 1fr;
        align-items: center;
        @include m.tablet {        
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            align-items: start;
        }
        gap: 4rem;
    }
    &__tecnicas {
        margin: 0;
        padding: 0;
        padding: 1rem;
    }
    &__titulo-h3 {
        margin: 0;
        text-align: center;
        color: v.$letrasDos;
        padding: 1rem 0 2rem 0;
        text-transform: capitalize;
        font-size: 2.4rem;
        @include m.tablet {
            text-align: start;
        }
    }


    &__profesionales {
        margin: 0;
        padding: 0;
        padding: 1rem;
    }
    &__tecnicas-panel,
    &__profesionales-panel {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        margin-bottom: 1rem;
    }
    &__nombre {
        color: v.$fondoDos;
        display: block;
        padding: 0;
        margin: 0;
        font-size: 1.8rem;
        font-weight: 400;
    }
}

:root {
    --habilidades-bar-lenght:0%;
}

.barra {
    width: 90%;
    display: grid;
    grid-template-columns: 10% 90%;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
    
    margin-top: 0.5rem;
    padding: 0.4rem 2rem;
    border-radius: 4px;
    background-color: lighten(v.$primario, 10);
    &__porcentaje {
        padding: 0.4rem;
        margin: 0;
        color: v.$letrasTitulo;
        font-weight: 700;
        font-size: 1.5rem;
        background-color: lighten(v.$primario, 10);
    }
    &__bar,
    &__bar::after {
        position: relative;
        background-color: v.$secundario;
        width: 100%;
        height: 14px;
        border-radius: 8px;
        overflow: hidden;
    }
    &__bar::after {
        content: "";
        position: absolute;
        left: -100%;
        background-color: v.$letrasTitulo;
        transition: 0.3s ease-in-out;
        animation: habilidades-barra 2s ease-in-out forwards;
    }
    &__bar--0::after {
        --habilidades-bar-lenght:0%;
    }
    &__bar--5::after {
        --habilidades-bar-lenght:5%;
    }
    &__bar--10::after {
        --habilidades-bar-lenght:10%;
    }
    &__bar--15::after {
        --habilidades-bar-lenght:15%;
    }
    &__bar--20::after {
        --habilidades-bar-lenght:20%;
    }
    &__bar--25::after {
        --habilidades-bar-lenght:25%;
    }
    &__bar--30::after {
        --habilidades-bar-lenght:30%;
    }
    &__bar--35::after {
        --habilidades-bar-lenght:35%;
    }
    &__bar--40::after {
        --habilidades-bar-lenght:40%;
    }
    &__bar--45::after {
        --habilidades-bar-lenght:45%;
    }
    &__bar--50::after {
        --habilidades-bar-lenght:50%;
    }
    &__bar--55::after {
        --habilidades-bar-lenght:55%;
    }
    &__bar--60::after {
        --habilidades-bar-lenght:60%;
    }
    &__bar--65::after {
        --habilidades-bar-lenght:65%;
    }
    &__bar--70::after {
        --habilidades-bar-lenght:70%;
    }
    &__bar--75::after {
        --habilidades-bar-lenght:75%;
    }
    &__bar--80::after {
        --habilidades-bar-lenght:80%;
    }
    &__bar--85::after {
        --habilidades-bar-lenght:85%;
    }
    &__bar--90::after {
        --habilidades-bar-lenght:90%;
    }
    &__bar--95::after {
        --habilidades-bar-lenght:95%;
    }
    &__bar--100::after {
        --habilidades-bar-lenght:100%;
    }
    &__tiempo {
        margin: 0;
        padding: 0.4rem;
        color: v.$letrasDos;
        font-size: 1.2rem;
    }
    &__medidas {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 0 1rem;
        margin-bottom: -5rem;
        z-index: 1;
    }
    &__medidas-p {
        color: v.$letras;
        font-size: 1.2rem;
        font-weight: 700;
        margin: 0;
        text-align: right;
    }
}

@keyframes habilidades-barra {
    to {
        transform: translateX(var(--habilidades-bar-lenght))
    }
}